import React from "react";
import ReactDOM from "react-dom";
import App from "./App";
import { Wallet } from "./near-wallet";
import { Contract } from "./near-interface";

// const reactRoot = createRoot(document.querySelector("#root"));

import { mungeBlockchainCrossword } from "./utils";

// create the Wallet and the Contract
const contractId = process.env.CONTRACT_NAME;
const wallet = new Wallet({ contractId: contractId });
const contract = new Contract({ wallet: wallet });

let data;
let solutionHash;

window.onload = wallet
  .startUp()
  .then(async ({ isSignedIn }) => {
    const chainData = await getUnsolvedPuzzles();
    // There may not be any crossword puzzles to solve, check this.
    if (chainData.puzzles.length) {
      solutionHash = chainData.puzzles[0]["solution_hash"];
      data = mungeBlockchainCrossword(chainData.puzzles);
    } else {
      console.log("Oof, there's no crossword to play right now, friend.");
    }
    return { isSignedIn, solutionHash, data };
  })
  .then(({ isSignedIn, solutionHash, data }) => {
    ReactDOM.render(
      <App
        contract={contract}
        wallet={wallet}
        isSignedIn={isSignedIn}
        solutionHash={solutionHash}
        data={data}
      />,
      document.getElementById("root")
    );
  })
  .catch((e) => {
    ReactDOM.render(
      <div style={{ color: "red" }}>
        Error: <code>{e.message}</code>
      </div>,
      document.getElementById("root")
    );
    console.error(e);
  });
